import React from 'react';
import injectSheet from 'react-jss';

import TopSection from '../components/TopSection';
import Section from '../components/Section';
import Link from '../components/Link';

const maxWidth = 1130;
const topSectionHeight = '39rem';

const styles = {
    download: {
        width: '23.5em',
        height: '3.53em',
        border: 'none',
        backgroundColor: '#61B0E0'
    }
};

export default injectSheet(styles)(({ classes, data }) => {
    return (
        <div>
            <TopSection
                maxWidth={maxWidth}
                height={topSectionHeight}
                sizesBackground={data.imageBackground.childImageSharp.sizes}
                justifyContent='center'>
                <h1>{data.contents.childStringsToml.home.title}</h1>
                <p>{data.contents.childStringsToml.home.description}</p>
                <Link to="/downloads">
                    <button className={classes.download} type="button">
                        {data.contents.childStringsToml.home.download}
                    </button>
                </Link>
            </TopSection>
            <Section maxWidth={maxWidth}>
                features
            </Section>
        </div>
    );
});

export const query = graphql`
    query IndexHomeQuery {
        ...IndexLayoutFragment
        ...IndexHomeFragment
    }

    fragment IndexHomeFragment on RootQueryType {
        imageBackground: file(relativePath: { eq: "bg_home.jpg" }) {
            childImageSharp {
                sizes(maxWidth: 1130) {
                    ...GatsbyImageSharpSizes
                }
            }
        }

        contents: file(relativePath: { eq: "strings.toml" }) {
            childStringsToml {
                home {
                    title
                    description
                    download
                }
            }
        }
    }
`;



